@import url(//at.alicdn.com/t/font_1777205_33rxoqzibzn.css);
.logo-container {
  text-align: center;
  margin: 10px 0;
}
.logo-container .logo-img {
  width: 150px;
  height: 150px;
}

.note-list {
  padding-top: 45px;
}
.note-list .add-icon {
  position: fixed;
  bottom: 20px;
  right: 20px;
  color: #ffac40;
  font-size: 50px;
  cursor: pointer;
}
.note-list .am-card {
  position: relative;
}
.note-list .am-card .am-checkbox-wrapper {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  text-align: right;
}
.note-list .am-card .am-checkbox-wrapper .am-checkbox {
  top: 50%;
  right: 30px;
}
.note-list .am-card .am-card-header-content {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.note-list .am-card .am-card-body {
  min-height: 20px;
  padding-top: 10px;
}
.note-list .am-card .am-card-body div {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.note-list .am-navbar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 150;
  background-color: #f7f7f7;
}
.note-list .am-navbar .am-navbar-left .am-icon {
  cursor: pointer;
}
.note-list .am-navbar .am-navbar-left .icon-PC_tuichudenglu {
  color: #aaa;
  font-size: 20px;
  cursor: pointer;
}
.note-list .am-navbar .manage {
  cursor: pointer;
  color: black;
}
.note-list .am-navbar .icon-piliangguanli {
  font-size: 20px;
  cursor: pointer;
  color: black;
}
.am-list-header {
  padding: 15px 0 9px 0;
  display: flex;
}
.am-list-header .am-list-item {
  background-color: #eaeaea;
  border-radius: 50px;
  flex: 7 1;
}
.am-list-header .am-list-item .am-list-line .am-input-label {
  width: 22px;
  padding-right: 10px;
  color: #bbb;
  position: relative;
  bottom: -3px;
}
.am-list-header .am-list-item .am-list-line input:disabled {
  background-color: inherit;
}
.am-list-header .cancel {
  flex: 1 1;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  color: #ff6600;
  cursor: pointer;
}
.manage-tool {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 45px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-top: 1px #aaa solid;
  background-color: #fff;
  z-index: 1000;
}
.manage-tool div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.manage-tool div span {
  cursor: pointer;
}
.active {
  color: #ffac40 !important;
}
.single-foo-menu {
  position: fixed;
  width: 100%;
  z-index: 200;
}
.single-foo-menu .am-menu .am-flexbox .am-flexbox-item:last-child {
  background-color: #f7f7f7;
}
.single-top-nav-bar {
  position: relative;
  background-color: #008AE6;
  color: #FFF;
}
.menu-mask {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.4;
  z-index: 89;
}
.category-selector {
  color: #ffac40;
  cursor: pointer;
  font-weight: bold;
}
.am-action-sheet-button-list-item {
  overflow: hidden;
}
.am-action-sheet-body {
  max-height: 70vh;
  overflow-y: scroll;
}

.toolbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.toolbar .icon-container {
  cursor: pointer;
  padding: 5px;
}
.toolbar .icon-container .iconfont {
  color: #555;
}
.toolbar .active .iconfont {
  color: #ffac40;
}

.editor-wrapper {
  background-color: #ffffff;
}
.editor-wrapper .editor-header {
  position: fixed;
  top: 0;
  width: 100%;
}
.editor-wrapper .editor-header span {
  cursor: pointer;
}
.editor-wrapper .editor-header .grey {
  color: #ccc;
}
.editor-wrapper .editor-header .edit-time {
  color: #ccc;
  font-size: 14px;
}
.editor-wrapper .editor-container {
  margin: 80px 10px 0 10px;
  font-size: 16px;
  min-height: calc(100vh - 80px);
}
.editor-wrapper .editor-container .DraftEditor-editorContainer {
  padding-bottom: 10px;
}
.editor-wrapper .editor-container .public-DraftEditorPlaceholder-inner {
  position: absolute;
  color: #ccc;
  pointer-events: none;
}
.editor-wrapper .toolbar {
  position: fixed;
  top: 45px;
  bottom: 0;
  background-color: #ffffff;
  width: 100%;
  height: 35px;
}
.editor-wrapper.hide-placeholder .public-DraftEditorPlaceholder-inner {
  display: none;
}

#root {
  background-color: #f7f7f7;
}

