.editor-wrapper {
  background-color: #ffffff;
}
.editor-wrapper .editor-header {
  position: fixed;
  top: 0;
  width: 100%;
}
.editor-wrapper .editor-header span {
  cursor: pointer;
}
.editor-wrapper .editor-header .grey {
  color: #ccc;
}
.editor-wrapper .editor-header .edit-time {
  color: #ccc;
  font-size: 14px;
}
.editor-wrapper .editor-container {
  margin: 80px 10px 0 10px;
  font-size: 16px;
  min-height: calc(100vh - 80px);
}
.editor-wrapper .editor-container .DraftEditor-editorContainer {
  padding-bottom: 10px;
}
.editor-wrapper .editor-container .public-DraftEditorPlaceholder-inner {
  position: absolute;
  color: #ccc;
  pointer-events: none;
}
.editor-wrapper .toolbar {
  position: fixed;
  top: 45px;
  bottom: 0;
  background-color: #ffffff;
  width: 100%;
  height: 35px;
}
.editor-wrapper.hide-placeholder .public-DraftEditorPlaceholder-inner {
  display: none;
}
